import { useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import { loginWithShopify, installShopify } from '../../../actions/loginActions';
import { useDispatch } from 'react-redux';
import { Loader } from "../../Layout/Main/CommonComponents/Loader";
import { syncWithShopify } from '../../../actions/productAction';

const useSearchParams = () => {
    const location = useLocation();
    const searchParams = new URLSearchParams(location.search);

    return {
        app_load_id: searchParams.get('app_load_id'),
        code: searchParams.get('code'),
        embedded: searchParams.get('embedded'),
        hmac: searchParams.get('hmac'),
        host: searchParams.get('host'),
        id_token: searchParams.get('id_token'),
        link_source: searchParams.get('link_source'),
        locale: searchParams.get('locale'),
        session: searchParams.get('session'),
        shop: searchParams.get('shop'),
        timestamp: searchParams.get('timestamp'),
    };
};

const removeNullParams = (obj) => {
    return Object.fromEntries(
        Object.entries(obj).filter(([_, value]) => value !== null && value !== undefined)
    );
};
const loadScript = (src) =>
    new Promise((resolve, reject) => {
        const script = document.createElement('script');
        script.src = src;
        script.onload = resolve;
        script.onerror = reject;
        document.head.appendChild(script);
    });

const ShopifyInstall = ({ history, isInstall }) => {
    const params = useSearchParams();
    const cleanParams = removeNullParams(params);
    const dispatch = useDispatch();
    const isShopifyLoad = () => {
        Promise.all([
            loadScript('https://unpkg.com/@shopify/app-bridge@3'),
            loadScript('https://unpkg.com/@shopify/app-bridge-utils@3'),
        ]).then(() => {
            if(localStorage.getItem('syncShopify') == 'true') dispatch(syncWithShopify({...cleanParams, guestUseShopify:'guest', ownerId: localStorage.getItem('ownerId')}))
            else dispatch(loginWithShopify(cleanParams, history))
        }).catch(err => console.error('Error loading scripts:', err));
    }


    useEffect(() => {
        if (isInstall) {
            if (cleanParams.embedded) {
                isShopifyLoad()
            } else {
                installShopify(cleanParams.shop);
            }
        } else {
            isShopifyLoad()
        }
    }, []);

    return <div className='h-100vh w-100 f-center'><Loader />Test taggshop</div>;
};

export default ShopifyInstall


